.card2{
    background-color: #f5f5f5;
    min-width: 60vmax;
    min-height: 80vw;
    align-items: flex-start;
    justify-content: space-around;
}
.card2:hover{
    cursor:default;
    border:0px;
    background-color: #f5f5f5;
}
.card2 .contenido .nombre {
    font-size: 2vmax;
}
.card2 .contenido {
    align-items: flex-start;
}
.dg {
    font-family: "Montserrat";
    font-size: 1vmax;
    color: #e9540d;
}
.nombre {
    margin-bottom: 5vmax;
    margin-top: 4vw;
}
.titulo {
    font-size: 1vmax;
}
.bold {
    font-family: "Montserrat-Bold";
}
.absolute {
    position: fixed;
    width: 40vw;
    opacity: 0.15;
    filter: alpha(opacity=15);
    left: 85vw;
}
.marcadeagua {
    position: relative;
}
.telefono {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: flex-start;
    color: #54575a;
    font-size: 1vmax;
}
.datosContainer {
    min-width: 50vmax;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.subContainer {
    background-color: #f5f5f5;
    width: 90vw;
    display: flex;
    flex-direction: column;
    padding-top: 5vh;
    padding-bottom: 5vh;
    align-items: center;
}
.PerfilContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #f5f5f5;
    min-height: 80vh;
}