.registerContainer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background-color: #f5f5f5;
    min-height: 80vh;
}
.tituloRegister {
    font-size: 3vw;
    text-align: center;
    margin: 1vh;
    color: #364049;
    margin-top: 5vh;
}
.formulario {
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}
.formulario input::placeholder {
    color: #364049;
}
.botonRegistrar {
    margin-top: 7vh;
    color: white;
    background-color: #e9540d;
    border-radius: 10px;
    padding: 1vh 3vw;
    font-size: 1.5vw;
    border-style: none;
    cursor: pointer;
}
.form-input{
    margin-top: 10px;
    min-width: 320px;
    width: 40vw;
    min-height: 2vw;
    border-radius: 10px;
    padding: 1vh;
    font-size: 2vh;
}
.coll {
    margin-bottom: 3vh;
}
