.card {
    background-color: white;
    min-height: 18vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    max-width: 60vw;
    }
.card img {
    max-width: 10vw;
}
.card svg {
    display: flex;
    align-self: center;
}
.card:hover {
    border: .2vw solid #e9540d;
    border-left: 0px;
    border-right: 0px;
    cursor: pointer;
    border-radius: .3vw;
}
.nombre {
    display: flex;
    font-family: 'Montserrat-Bold';
    font-size: 13px;
    color: #e9540d;
    text-shadow: none;
}
.telefono {
    font-size: 8px;
    color: #f5b333;
    text-shadow: none;
}
.web {
    font-size: 8px;
    text-align: start;
    color: #f5b333;
    text-shadow: none;
}
.contenido {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    min-width: 45vw;
}
.gris {
    color: #b2b1b1;
    justify-self: center;
    text-align: end;
    font-size: 2vw;
}
.paginas {
display: flex;
flex-direction: row;
justify-content: space-around;
margin-top: 10px;
background-color: white;
color: black;
}
a:hover {
    cursor: pointer;
}




.transportistasList {
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
.absolute {
    position: fixed;
    width: 40vw;
    opacity: 0.15;
    filter: alpha(opacity=15);
    left: 85vw;
}
.marcadeagua {
    position: relative;
}
.transportistasList::-webkit-scrollbar {
    -webkit-appearance: none;
}

.transportistasList::-webkit-scrollbar:vertical {
    width:10px;
}

.transportistasList::-webkit-scrollbar-button:increment,.contenedor::-webkit-scrollbar-button {
    display: none;
}

.transportistasList::-webkit-scrollbar:horizontal {
    height: 10px;
}

.transportistasList::-webkit-scrollbar-thumb {
    background-color: #797979;
    border-radius: 20px;
}

.transportistasList::-webkit-scrollbar-track {
    border-radius: 10px;
}
.placeholderloco {
    display: flex;
    flex-direction: row;
    max-width: 60vw;
    margin-left: 20vw;
    margin-top: 10vw;
}