:root {
    --background-color: #53575a;
}
.col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.container{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background-color: white;
    min-height: 80vh;
}
.row {
    display: flex;
    flex-direction: row;
}
.row-reverse {
    flex-direction: row-reverse;
}

.Light {
    background-color: #53575a;
    color: white;
}
.Dark {
    background-color: white;
    color: #53575a;
}
.btnLightMode {
    width: 5vh;
    height: 5vh;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    bottom: 5vw;
    left: 3vh;
    transition: all 500ms ease;
    z-index: 1000;
}
.btnDark{
    border-radius: 5px;
    border-style: solid;
    border-color: black;
    box-shadow: 0px .3vh 1vh white;
    transition:all .3s ease-in-out;
}
.btnDark:hover {
    cursor: pointer;

-webkit-border-radius:50px;
box-shadow: 0px 0px 15px 15px black;
-webkit-box-shadow: 0px 0px 10px 3px black;
}
.btnLight{
    border-radius: 50px;
    border-style: solid;
    border-color: black;
    border: 1px solid black;
    box-shadow: 10px 10px white;
}
.btnLight:hover {
    cursor: pointer;
}
.btnDarkMode {
    z-index: 1000;
    width: 5vh;
    height: 5vh;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    bottom: 5vw;
    left: 3vh;
    transition: all 500ms ease;
}

.btnDarkMode:hover {
    box-shadow: 0px 0px 100px 400px white;
    transition: all 500ms ease;
}
.btnLightMode:hover {
    box-shadow: 0px 0px 100px 400px #53575a;
    transition: all 500ms ease;
}
