.homeContainer {
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.rsac1 {
    border-radius: 7px!important;
    z-index: 10;
    min-width: 350px;
    background-color: #cbcdcf;
}
.rsac2 {
    border-radius: 7px!important;
    z-index: 9;
    min-width: 350px;
    background-color: #cbcdcf;
}
.rsac1 div {
    border-radius: 7px!important;
    background-color: #cbcdcf;
}
.rsac2 div {
    border-radius: 7px!important;
    background-color: #cbcdcf;
}
.buscar {
    width: 10vmin;
    min-width: 150px;
    height: 5vmin;
    cursor: pointer;
    text-shadow: 3px 3px 3px black;
    text-align: center;
    border-style: solid;
    border-radius: 10px;
    border-color: #cbcdcf;
}

.buscar div {
    font-size: 1.5vmax;
}
.wrapper {
    border-radius: 7px!important;
    background-color: #cbcdcf;
}
.wrapper div {
    background-color: #cbcdcf;
}
.wrapper div input {
    color: #364049;
}
