.btnSecundario {
    display: none;
}
a:hover {
    color: black;
}
.btnTransportista {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    bottom: 5vw;
    border: none;
    padding: 0px 2vh;
}
.LappaLogo {
    background: url(http://hgtarg.com/logo1.png) no-repeat center center;
    background-size: 30vmin;
    -moz-background-size: 30vmin;
    -webkit-background-size: 30vmin;
    -o-background-size: 30vmin;
    width: 20vmax;
    height: 20vh;
    margin-left: 10px;
    margin-right: 10px;
}
.LappaLogo:hover{
    cursor: pointer;
}
.navBar {
    width: 100%;
    height: 18vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #FF5100;
    border-bottom: #F9B233 solid 2vh;
    margin-top: 0px;
    z-index: 100;
}
.transp {
    background-color: transparent;
    box-shadow: none;
    border-bottom: none;
}
.navButtons {
    width: 40vw;
    justify-content: space-evenly;
}
.navButtons p {
    font-size: 2.2vw;
    text-shadow: none;
    font-family: 'Montserrat-Bold';
}
.navButtons a {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}