.transpContainer {
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: #f5f5f5;
    min-height: 80vh;
}
.boton{
    width: 30vmax;
    color: #364049;
    cursor: pointer;
    border-color: #364049;
    border-radius: 15px;
    border-style: solid;
    text-align: center;
    padding: 1vmax;
    background-color: white;
}